@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@200;300&display=swap);
:root {
  --main-color: #fff;
  --main-bg-color: #222;
  --accent-color: #398cd4;
  --error-color: #ffa3a3;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Mukta', sans-serif;
  font-size: 18px;
  color: #fff;
  color: var(--main-color);
  background-color: #222;
  background-color: var(--main-bg-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  border: none;
  cursor: pointer;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

h2,
p {
  margin: 0;
}



.btn {
  color: var(--main-color);
  background-color: var(--accent-color);
  padding: 12px 15px;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.column {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.text-center {
  text-align: center;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 300px;
  margin: 0 auto;
  padding: 0 10px;
  grid-gap: 50px;
  gap: 50px;
}

.title {
  border-bottom: 1px solid #fff;
}

.icon-group {
  float: right;
}

.section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 5px 0;
  grid-gap: 5px;
  gap: 5px;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.field {
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 8px 10px;
  width: 100%;
}

.field[type='date'] {
  width: 220px;
}

.date-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.btn {
  width: 100%;
  max-width: 100px;
}

.btn[type='submit'] {
  background-color: rgb(48, 133, 83);
  max-width: none;
  max-width: initial;
}

.error {
  background-color: var(--error-color);
}

@media screen and (min-width: 760px) {
  .container {
    max-width: 500px;
  }
}

